.option {
    display: flex;
    justify-content: space-between;
    padding: $l-size $m-size;
    border-bottom: 1px solid lighten($light-blue, 10%);
    &__text {
        margin: 0;
        font-weight: 500;
        font-size: 2rem;
        word-break: break-all;
        color: white;
    }
}